.home{
    display: flex;
    justify-content: center;
    margin: 0 auto;
    max-width: 1500px;
    &__container{
        .banner{
            width: 100%;
            z-index: -1;
            margin-bottom: -150px;
            mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
        }
        &--row{
            display: flex;
            margin: 0 5px;
        }
    }
}